.m-list{
	width: 100%;
	font-size: 2.8rem;

	&__item{
		box-sizing: border-box;
		display: block;
		padding: 30px 20px;
		background: url(../img/icon-list-arrow.svg) right center no-repeat;
		background-size: 38px auto;
		border-bottom: 1px solid #d6d6d6;
		user-select: none;

		>*{
			pointer-events: none;
		}

		h2{
			font-size: 3.4rem;
			font-weight: bold;
		}

		h3{
			font-weight: bold;
			font-size: 2.4rem;
		}

		.__item-head{
			display: flex;
			justify-content: space-between;
			align-items: center;
			margin-bottom: 20px;
		}

		.__category{
			width: 150px;
			color: #fff;
			background: #db2727;
			padding: 5px 0;
			line-height: 1;
			font-size: 2.4rem;
			border-radius: 5px;
			text-align: center;
		}

		.__item-body{
			display: flex;
		}

		.__thumb{
			width: 240px;
			margin-right: 20px;

			img{
				display: block;
				width: 100%;
				height: auto;
			}
		}

		.__summary{
			display: flex;
			flex-wrap: wrap;
			align-items: center;
			width: 400px;

			>*{
				box-sizing: border-box;
				flex-basis: auto;
				flex-grow: 0;
				flex-shrink: 0;
				margin: 0 20px 0 0;
				border-top:1px solid #e5e5e5;

				&:nth-child(-n+2){
					border-top: 0;
				}
				&:nth-child(n+3){
					padding-top: 10px;
				}
			}

			h3{
				margin-bottom: 0.25em;
			}
		}

	}
}