.content-capture--start {

	.content-head{
		padding: 20px;
		font-size: 3.2rem;
	}

	.__ruby{
		margin-bottom: 10px;
		&:empty{
			display: none;
		}
	}

	.__keywords{
		display: flex;
		> *{
			position: relative;
			box-sizing: border-box;
			flex-basis: auto;
			flex-grow: 0;
			flex-shrink: 0;

			&:first-child{
				flex-grow: 1;
				margin-right: 20px;
			}
		}

		&__input{
			input{
				display: block;
				box-sizing: border-box;
				width: 100%;
				height: 63px;

				padding-left: 15px;
				padding-right: 63px;

				color: #666;
				background-color: #e5e5e5;
				border: 0;

				pointer-events: none;
			}
		}

		&__submit{
			display: block;
			width: 63px;
			height: 63px;
			background: #f89a00;
			border-radius: 5px;
			border: 0;
			margin: 0;
			padding: 0;
		}

		&__clear{
			display: block;
			position: absolute;

			top:0;
			right:0;

			width: 63px;
			height: 63px;
		}
	}

	// Override
	.content-body__inner {
		display: flex;
		flex-direction: column;
		height: 100%;
		overflow: hidden;

		> div:first-child {
			position: relative;
			flex-basis: auto;
			flex-grow: 1;
			flex-shrink: 0;
		}

		.copyright {
			flex-grow: 0;
			flex-shrink: 0;
		}
	}

	.__capture{
		display: block;
		position: absolute;
		top: 0;
		left: 0;
		height: 100%;
		width: 100%;
	}

	.__capture-dummy{
		display: flex;
		justify-content: center;
		align-items: center;
		color: #666;
		width: 100%;
		height: 100%;
		background: #000;
	}

	.m-ui-file{
		position: absolute;
		right: 20px;
		bottom: 20px;
	}
}
