.content-capture--detail {

	h2{
		font-size: 3.0rem;
		color: #db2727;
		margin-bottom: 20px;
	}
	.l-section {
		border-top: 1px solid #000000;
		padding: 30px 20px;
		margin: auto;

		&--gray{
			background: #f2f2f2;
		}
		&--nb{
			border: 0;
		}
	}

	.l-section-part{
		margin: 30px 0;
	}

	.__result-summary{
		background: #f2f2f2;
		border:0;
		padding: 0 20px;
		font-size: 3.2rem;

		h2{
			font-size: 2.4rem;
			color: #cb8922;
			font-weight: bold;
			margin-bottom: 10px;
		}

		> div{
			padding: 20px 0;
			border-bottom: 2px dotted #000;
			&:last-child{
				border: 0;
			}
		}

		.__words{
			> span{
				display: inline-flex;
				flex-direction: column;
			}

			&__ruby{
				font-size: 2.4rem;
				margin-bottom: 5px;
			}
		}
	}

	.__image-search{
		overflow: hidden;
		overflow-x: auto;
		white-space: nowrap;
	}

	.__dbpedia{
		margin: 20px 0;
	}

	.__recipe{
		&__item{
			display: flex;
			margin-bottom: 20px;

			>*{
				flex-basis: auto;
				flex-grow: 0;
				flex-shrink: 0;
				overflow: hidden;
				pointer-events: none;
			}

			> div:first-child{
				margin-right: 20px;
			}

			> div:last-child{
				width: 315px;
			}

			h3{
				font-size: 3.0rem;
				margin:0;
				padding:0;
				font-weight: bold;
			}
			h4{
				font-size: 2.4rem;
				color: #6c6c6c;
				margin-bottom: 20px;
			}
			p{
				margin:0;
			}

			img{
				display: block;
			}
		}
	}

	.__video{
		&__item{
			display: flex;
			margin-bottom: 20px;

			>*{
				flex-basis: auto;
				flex-grow: 0;
				flex-shrink: 0;
				overflow: hidden;
				pointer-events: none;
			}

			> div:first-child{
				margin-right: 20px;
			}

			> div:last-child{
				width: 315px;
			}

			p{
				margin:0;
			}

			img{
				display: block;
			}
		}
	}

}