.content-fiver--spot {

	h2{
		font-size: 3.0rem;
		color: #db2727;
		margin-bottom: 20px;
	}

	.l-section {
		border-top: 1px solid #000000;
		padding: 30px 20px;
		margin: auto;
	}

	.l-section-part{
		margin: 30px 0;
	}

	.__slide-indicator{
		text-align: center;
		padding: 15px 0;
	}

	.__image-search{
		overflow: hidden;
		overflow-x: auto;
		white-space: nowrap;
	}

	.__recipe{
		&__item{
			display: flex;
			margin-bottom: 20px;

			>*{
				flex-basis: auto;
				flex-grow: 0;
				flex-shrink: 0;
				overflow: hidden;
				pointer-events: none;
			}

			> div:first-child{
				margin-right: 20px;
			}

			> div:last-child{
				width: 315px;
			}

			h3{
				font-size: 3.0rem;
				margin:0;
				padding:0;
				font-weight: bold;
			}
			h4{
				font-size: 2.4rem;
				color: #6c6c6c;
				margin-bottom: 20px;
			}
			p{
				margin:0;
			}

			img{
				display: block;
			}
		}
	}

	.__video{
		&__item{
			display: flex;
			margin-bottom: 20px;

			>*{
				flex-basis: auto;
				flex-grow: 0;
				flex-shrink: 0;
				overflow: hidden;
				pointer-events: none;
			}

			> div:first-child{
				margin-right: 20px;
			}

			> div:last-child{
				width: 315px;
			}

			p{
				margin:0;
			}

			img{
				display: block;
			}
		}
	}

}