* {
  font-family: inherit;
  max-height: 99999999em;
}

html, body {
  height: 100%;
  overflow: hidden;
}

html {
  font-size: 10px;
}

body {
  font-family: Avenir, "Open Sans", "Helvetica Neue", Helvetica, Arial, Verdana, Roboto, "Meiryo UI", "メイリオ", "游ゴシック", "Yu Gothic", "游ゴシック体", "YuGothic", "ヒラギノ角ゴ Pro W3", "Hiragino Kaku Gothic Pro", Meiryo, "ＭＳ Ｐゴシック", "MS PGothic", sans-serif;
  font-size: 2.6rem;
  color: #000;
  background: #fff;
  width: 640px;
  margin: 0 auto;
  line-height: 1;
  user-select: none;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0;
  font-weight: normal;
}

h2 {
  font-size: 56px;
}

a {
  color: inherit;
  text-decoration: inherit;
  -webkit-tap-highlight-color: transparent;
}

ruby {
  padding: 0 0.5em;
}

rt {
  font-size: 70%;
}

.container {
  position: relative;
  min-height: 100%;
  width: 640px;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
}

.container > * {
  flex-grow: 0;
  flex-shrink: 0;
  flex-basis: auto;
}

.page-body {
  flex-grow: 1;
  position: relative;
  overflow: hidden;
  height: 100%;
}

.content {
  position: absolute;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  height: 100%;
  width: 100%;
  background: #fff;
}

.content > * {
  flex-grow: 0;
  flex-shrink: 0;
  flex-basis: auto;
}

.content-head {
  background: #fff;
}

.content-body {
  position: relative;
  flex-grow: 1;
  overflow: hidden;
}

.content-body__scroll {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
}

.content-body__inner {
  *zoom: 1;
}

.content-body__inner:before, .content-body__inner:after {
  content: " ";
  display: table;
}

.content-body__inner:after {
  clear: both;
}

.copyright {
  text-align: center;
  color: #000;
  background: #fff;
  padding: 10px 0;
  font-size: 1.6rem;
}

.copyright a {
  color: #2a88e8;
}

.m-menu {
  position: relative;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  line-height: 1;
}

.m-menu__item {
  flex-grow: 1;
  flex-shrink: 1;
  flex-basis: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  min-height: 100px;
  color: #fff;
  background-color: #cb8922;
  text-align: center;
  vertical-align: bottom;
  overflow: hidden;
  border-left: 1px solid #e0b87a;
}

.m-menu__item:first-child {
  border-left: 0;
}

.m-menu__item._active {
  background-color: #a16910;
}

.m-menu__item > img {
  width: 46px;
  height: auto;
  margin-right: 10px;
}

.m-menu__item > span {
  font-size: 2.0rem;
}

.m-menu-targets {
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background: #fff;
}

.m-menu-target {
  position: relative;
  width: 100%;
  height: 100%;
  display: none;
  background: #fff;
}

.m-menu-target._active {
  display: block;
}

.m-ui-toggle {
  display: inline-block;
  position: relative;
  width: 4em;
  height: 2.5em;
  border-radius: 1.5em;
  background: #dedede;
  vertical-align: middle;
  box-shadow: inset 0 3px 5px rgba(0, 0, 0, 0.2);
  -webkit-tap-highlight-color: transparent;
  user-select: none;
}

.m-ui-toggle:before {
  position: absolute;
  content: "";
  display: block;
  width: 2.5em;
  height: 2.5em;
  top: 0;
  left: 0;
  background: #fff;
  border-radius: 50%;
  box-shadow: 0 3px 5px rgba(0, 0, 0, 0.2);
  transition: transform 100ms ease, background-color 100ms ease;
  pointer-events: none;
}

.m-ui-toggle._toggle-on {
  background: #ffd634;
}

.m-ui-toggle._toggle-on:before {
  transform: translate(60%, 0);
}

.m-ui-file {
  display: inline-block;
  position: relative;
  overflow: hidden;
  width: 140px;
  height: 140px;
  background: url(../img/icon-input-capture.svg) center center no-repeat;
  background-size: contain;
}

.m-ui-file input[type=file] {
  opacity: 0;
  -webkit-appearance: none;
  appearance: none;
  pointer-events: none;
}

.m-ui-button {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  margin: auto;
  border: 0;
  font-size: 4rem;
  font-weight: bold;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  overflow: hidden;
  padding: 0;
  background-color: #f89a00;
  color: #fff;
  border-radius: 10px;
}

.m-ui-button--search {
  display: flex;
  width: 520px;
  height: 120px;
}

.m-ui-button--search:before {
  display: inline-block;
  content: "";
  background: url(../img/icon-search.png) center center no-repeat;
  background-size: contain;
  margin-right: 10px;
  width: 1em;
  height: 1em;
}

.m-button--favorite {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  background: #d3332b;
  line-height: 1;
  padding: 10px 15px;
  border-radius: 3px;
}

.m-button--favorite:before {
  content: "";
  display: inline-block;
  width: 28px;
  height: 28px;
  margin-right: 10px;
  background: url(../img/afterfiver/icon-heart.svg) center center no-repeat;
  background-size: contain;
}

.m-button--list {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  min-height: 50px;
  color: #fff;
  background: #f89a00;
  line-height: 1;
  padding: 0 25px 0 15px;
  border-radius: 3px;
  font-size: 2.8rem;
}

.m-button--list:before {
  content: "";
  display: inline-block;
  width: 1em;
  height: 1em;
  margin-right: 10px;
  line-height: 1;
  background: url(../img/afterfiver/icon-list.svg) center center no-repeat;
  background-size: contain;
}

.m-button--map-app {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  min-height: 50px;
  color: #fff;
  background: #067bde;
  line-height: 1;
  padding: 0 25px;
  border-radius: 3px;
  font-size: 2.8rem;
}

.m-button--map-app:before {
  content: "";
  display: inline-block;
  width: 0;
  height: 1em;
  margin-right: 0;
  line-height: 1;
}

.m-list {
  width: 100%;
  font-size: 2.8rem;
}

.m-list__item {
  box-sizing: border-box;
  display: block;
  padding: 30px 20px;
  background: url(../img/icon-list-arrow.svg) right center no-repeat;
  background-size: 38px auto;
  border-bottom: 1px solid #d6d6d6;
  user-select: none;
}

.m-list__item > * {
  pointer-events: none;
}

.m-list__item h2 {
  font-size: 3.4rem;
  font-weight: bold;
}

.m-list__item h3 {
  font-weight: bold;
  font-size: 2.4rem;
}

.m-list__item .__item-head {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.m-list__item .__category {
  width: 150px;
  color: #fff;
  background: #db2727;
  padding: 5px 0;
  line-height: 1;
  font-size: 2.4rem;
  border-radius: 5px;
  text-align: center;
}

.m-list__item .__item-body {
  display: flex;
}

.m-list__item .__thumb {
  width: 240px;
  margin-right: 20px;
}

.m-list__item .__thumb img {
  display: block;
  width: 100%;
  height: auto;
}

.m-list__item .__summary {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  width: 400px;
}

.m-list__item .__summary > * {
  box-sizing: border-box;
  flex-basis: auto;
  flex-grow: 0;
  flex-shrink: 0;
  margin: 0 20px 0 0;
  border-top: 1px solid #e5e5e5;
}

.m-list__item .__summary > *:nth-child(-n+2) {
  border-top: 0;
}

.m-list__item .__summary > *:nth-child(n+3) {
  padding-top: 10px;
}

.m-list__item .__summary h3 {
  margin-bottom: 0.25em;
}

.m-sub-navigation {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px;
  border-bottom: 1px solid #d6d6d6;
}

.m-sub-navigation > * {
  flex-basis: 100%;
  flex-grow: 1;
  flex-shrink: 1;
}

.m-sub-navigation > *:nth-child(1) {
  text-align: left;
}

.m-sub-navigation > *:nth-child(2) {
  text-align: center;
  white-space: nowrap;
}

.m-sub-navigation > *:nth-child(3) {
  text-align: right;
}

.m-sub-navigation .__link-back:before {
  content: "";
  display: inline-block;
  width: 38px;
  height: 38px;
  background: url(../img/icon-back.svg) center center no-repeat;
  background-size: contain;
  margin-right: 10px;
}

.m-sub-navigation .__link-map {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  min-height: 50px;
  color: #fff;
  background: #f89a00;
  line-height: 1;
  padding: 0 25px 0 15px;
  border-radius: 3px;
  font-size: 2.8rem;
}

.m-sub-navigation .__link-map:before {
  content: "";
  display: inline-block;
  width: 1em;
  height: 1em;
  margin-right: 10px;
  line-height: 1;
  background: url(../img/icon-map-pin.svg) center center no-repeat;
  background-size: contain;
}

.content-fiver--start {
  *zoom: 1;
  position: relative;
  box-sizing: border-box;
  color: #fff;
  background: #fff url(../img/afterfiver/bg.jpg) center bottom no-repeat;
  background-size: 100% auto;
  background-attachment: fixed;
}

.content-fiver--start:before, .content-fiver--start:after {
  content: " ";
  display: table;
}

.content-fiver--start:after {
  clear: both;
}

.content-fiver--start h2 {
  text-align: center;
  color: #ffe534;
  margin: 20px 0;
  font-weight: bold;
}

.content-fiver--start select {
  font-size: 3.2rem;
  text-align: -webkit-center;
  min-width: 384px;
}

.content-fiver--start .content-body__inner {
  padding-top: 30px;
}

.content-fiver--start .l-section {
  padding: 0 20px;
  margin: auto;
  text-align: center;
}

.content-fiver--start .l-section:before {
  content: "";
  display: block;
  width: 640px;
  height: 52px;
  background: url(../img/afterfiver/sep.png) center center no-repeat;
  margin: 60px 0 0 -20px;
}

.content-fiver--start .l-section:first-child:before {
  display: none;
}

.content-fiver--start .l-section-part {
  margin: 30px 0;
}

.content-fiver--start ._layout1 {
  display: inline-flex;
  align-items: center;
  margin: 20px 0;
}

.content-fiver--start ._layout1 > :nth-child(2) {
  margin-left: 10px;
  font-size: 3.2rem;
}

.content-fiver--start ._layout2 {
  display: table;
  text-align: center;
  margin: auto;
}

.content-fiver--start ._layout2 h3 {
  text-align: left;
  margin-bottom: 10px;
}

.content-fiver--start ._layout3 {
  display: flex;
  align-items: center;
  border-top: 1px solid rgba(255, 255, 255, 0.5);
  border-bottom: 1px solid rgba(255, 255, 255, 0.5);
  padding: 10px 0;
}

.content-fiver--start ._layout3 > div:first-child {
  white-space: nowrap;
  overflow: hidden;
  overflow-x: auto;
  margin-right: 20px;
  background: rgba(0, 0, 0, 0.5);
  padding: 0.75em;
}

.content-fiver--start .m-ui-button--search {
  margin: 40px auto;
}

.content-fiver--list-map .content-body__inner {
  display: flex;
  flex-direction: column;
  height: 100%;
  overflow: hidden;
}

.content-fiver--list-map .content-body__inner > div:first-child {
  position: relative;
  flex-basis: auto;
  flex-grow: 1;
  flex-shrink: 0;
}

.content-fiver--list-map .content-body__inner .copyright {
  flex-grow: 0;
  flex-shrink: 0;
}

.content-fiver--list-map .__page-navigation {
  box-sizing: border-box;
  line-height: 1;
  padding: 10px 20px;
  text-align: right;
  position: absolute;
  width: 100%;
  margin: auto;
  pointer-events: none;
  z-index: 1;
}

.content-fiver--list-map .__page-navigation > * {
  pointer-events: auto;
}

.content-fiver--list-map .__map {
  position: absolute;
  width: 100%;
  height: 100%;
  background: gray;
}

.content-fiver--list-map .__spot-desc {
  position: absolute;
  bottom: 10px;
  left: 0;
  width: 100%;
  display: table;
}

.content-fiver--list-map .__spot-desc > div {
  display: table-cell;
}

.content-fiver--list-map .__spot-desc > div:nth-child(1), .content-fiver--list-map .__spot-desc > div:nth-child(3) {
  width: 49px;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.7);
  vertical-align: middle;
}

