.content-tv--start {
	h1 {
		font-size: 4.6rem;
		font-weight: bold;
		text-align: center;
		padding: 40px 0 30px;

		span {
			position: relative;
			top: -0.2em;
			display: inline-block;
			color: #fff;
			background-color: #d42800;
			font-size: 2.6rem;
			padding: 10px 20px;
			margin-right: 5px;
			vertical-align: middle;
		}
	}
	h2{
		font-size: 3.0rem;
		color: #db2727;
		margin-bottom: 20px;
	}

	.l-section {
		border-top: 1px solid #000000;
		padding: 30px 20px;
		margin: auto;
	}

	.l-section-part{
		margin: 30px 0;
	}

	.__screenshot{

		margin-bottom: 30px;

		&__image{
			img{
				width: 100%;
				height: auto;
			}
			margin-bottom: 10px;
		}

		&__caption{
			margin: 0 20px;
			font-size: 3.0rem;
		}
	}

	.__twitter{

		padding: 0 60px;
		margin-bottom: 40px;

		&__head{
			color: #fff;
			background: #429ee3 url(../img/tv/twitter-logo.png) left top no-repeat;
			font-size: 3.0rem;
			line-height: 51px;
			padding-left: 67px;
		}

		&__body{
			border: 1px solid #dcdcdc;
		}
	}

	.__video{
		&__item{
			display: flex;
			margin-bottom: 20px;

			>*{
				flex-basis: auto;
				flex-grow: 0;
				flex-shrink: 0;
				overflow: hidden;
				pointer-events: none;
			}

			> div:first-child{
				margin-right: 20px;
			}

			> div:last-child{
				width: 315px;
			}

			p{
				margin:0;
			}

			img{
				display: block;
			}
		}
	}

}