.content-fiver--start {

	@include cf();

	position: relative;
	box-sizing: border-box;
	color: #fff;

	background: #fff url(../img/afterfiver/bg.jpg) center bottom no-repeat;
	background-size: 100% auto;
	background-attachment: fixed;

	h2{
		text-align: center;
		color: #ffe534;
		margin: 20px 0;
		font-weight: bold;
	}

	select {
		font-size: 3.2rem;
		text-align: -webkit-center;
		min-width: 384px;
	}

	.content-body__inner{
		padding-top: 30px;
	}

	.l-section {

		padding: 0 20px;
		margin: auto;
		text-align: center;

		&:before {
			content: "";
			display: block;
			width: 640px;
			height: 52px;
			background: url(../img/afterfiver/sep.png) center center no-repeat;
			margin: 60px 0 0 -20px;
		}

		&:first-child:before {
			display: none;
		}
	}

	.l-section-part{
		margin: 30px 0;
	}

	._layout1{
		display: inline-flex;
		align-items: center;
		margin: 20px 0;

		> :nth-child(2){
			margin-left: 10px;
			font-size: 3.2rem;
		}
	}

	._layout2{

		display: table;
		text-align: center;
		margin: auto;

		h3{
			text-align: left;
			margin-bottom: 10px;
		}
	}

	._layout3{
		display: flex;
		align-items: center;

		border-top: 1px solid rgba(255,255,255,0.5);
		border-bottom: 1px solid rgba(255,255,255,0.5);
		padding: 10px 0;

		> div:first-child{
			white-space: nowrap;
			overflow: hidden;
			overflow-x: auto;
			margin-right: 20px;
			background: rgba(0,0,0,0.5);
			padding: 0.75em;
		}
	}

	.m-ui-button--search{
		margin: 40px auto;
	}

}