@mixin cf{

	*zoom: 1;

	&:before,
	&:after {
		content: " ";
		display: table;
	}

	&:after {
		clear: both;
	}

}

@mixin screen-narrow {
	@media screen and (max-width: 480px) {
		font-size: 16px;
	}
}

@mixin screen-middle {
	@media screen and (min-width: 481px) and (max-width: 980px) {
		font-size: 16px;
	}
}

@mixin screen-wide {
	@media screen and (min-width: 981px){
		font-size: 16px;
	}
}

@mixin simple-gradient($rad:180deg) {
	background-image: linear-gradient($rad, $bgColor, #e8e8e8);
}